import { api, tags, urlUtils } from '@igblsln/store';
export const projectDetailsApi = api.injectEndpoints({
    overrideExisting: false,
    endpoints: (build) => ({
        listProjectPurchaseDetails: build.query({
            query: ({ projectId }) => {
                console.log(projectId);
                return {
                    url: urlUtils('/projectdetails', `?project_id=[${projectId || ''}]`),
                };
            },
            providesTags: (result = []) => [
                ...result.map(({ key: id }) => ({ type: tags.PROJECT_DETAILS, id })),
                { type: tags.PROJECT_DETAILS, id: 'LIST' },
            ],
        }),
        getErrorProne: build.query({
            query: () => 'error-prone',
        }),
    }),
});
export const { useListProjectPurchaseDetailsQuery, useGetErrorProneQuery } = projectDetailsApi;
